import { Directive, ElementRef, HostListener } from '@angular/core';

@Directive({
  selector: '[appDni]'
})
export class DniDirective {
  private specialKeys: Array<string> = ['Backspace'];
  private regexp: RegExp = new RegExp(/^[0-9]{0,8}$/g);

  constructor(private el: ElementRef) {}

  @HostListener('keydown', ['$event'])
  onKeyDown(event: KeyboardEvent) {
    let actual: string = this.el.nativeElement.value;
    let siguiente: string = actual.concat(event.key);

    if (this.specialKeys.indexOf(event.key) !== -1) {
      return;
    }

    if (siguiente && !String(siguiente).match(this.regexp)) {
      event.preventDefault();
    }
  }
}
